import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesProductComponent } from './sales-product.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: SalesProductComponent
  },
  {
    path: 'detail',
    loadChildren: () => import('./detail/detail.module').then(m => m.DetailModule)
  }
]

@NgModule({
  declarations: [SalesProductComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule
  ]
})
export class SalesProductModule { }
